#flyoutMenu{
    width:100vw;
    height: 100vh;
    background-color: rebeccapurple;
    position: fixed;
    top:0;
    left: 0;
    transition: transform .3s cubic-bezier(0.2, .2,.2,.2);
    overflow: scroll;
    z-index: 1000;
}

#flyoutMenu.hide{
    transform: translate3d(-100vw,0,0);
}
#flyoutMenu.show{
    transform: translate3d(0vw,0,0);
    overflow: hidden;
}